import React, { useEffect, useState } from 'react';

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { MdEdit } from 'react-icons/md';
import { FaEye } from 'react-icons/fa';

import DOMPurify from 'dompurify';
import { ReportAnalysisTypes } from '../types/ReportAnalysisTypes';

interface OwnProps {
  title: string;
  value: string;
  dataKey: keyof ReportAnalysisTypes;
  isEditEnabled: boolean;
  setValue: (dataName: keyof ReportAnalysisTypes, diagnosisText: string) => void;
}

type Props = OwnProps;

const DiagnosisSection: React.FC<Props> = ({ value, setValue, dataKey, title, isEditEnabled }) => {
  const [isEdit, setIsEdit] = useState(true);

  const [editorText, setEditorText] = useState(decodeURIComponent(value));

  const onChangeValue = (html: string) => {
    html = DOMPurify.sanitize(html);
    setEditorText(html);
    setValue(dataKey, encodeURIComponent(html));
  };

  useEffect(() => {
    setEditorText(decodeURIComponent(value));
  }, [value]);

  return (
    <div className="mb-3">
      <div className="flex items-center justify-between text-black">
        <h5 className="mb-2 text-sm font-bold">{title}</h5>
        <div>
          {isEditEnabled && !isEdit && (
            <MdEdit className="fill-green-600" size={20} onClick={() => setIsEdit(true)} />
          )}
          {isEditEnabled && isEdit && (
            <FaEye className="fill-blue-600" size={20} onClick={() => setIsEdit(false)} />
          )}
        </div>
      </div>
      {isEdit && isEditEnabled ? (
        <ReactQuill onChange={onChangeValue} className="text-black" value={editorText} />
      ) : (
        <div className="text-black" dangerouslySetInnerHTML={{ __html: editorText }} />
      )}
    </div>
  );
};

export default DiagnosisSection;
