import { useLocation } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { isStr } from '../utils';
import { setApiToken } from '../../storage';
export default function useToken() {
  const location = useLocation();
  const [token, setToken] = useState<string | null>(null);
  const [reportId, setReportId] = useState<number | null>(null);

  useEffect(() => {
    const params = new URLSearchParams(location.search);
    let query_token = params.get('bearer');
    query_token = decodeURIComponent(query_token);
    if (isStr(query_token)) {
      setApiToken(query_token);
      setToken(query_token);
    }
    const report_id = params.get('reportId');
    if (isStr(report_id)) {
      setReportId(Number(report_id));
    }
  }, [location.search]);

  return { token, reportId };
}
